import React, { Component } from 'react';
import { COUNTRIES } from '../shared/countries';
import Flags from './FlagsComponents';
import { Jumbotron, Button } from 'reactstrap';

class Game extends Component {
    constructor(props) {
        super(props);

        this.state = {
            countries: COUNTRIES,
            correct: 0,
            size: null,
            started: false,
            current: 0,
            chosenNumbers: null,
            cl1: "primary",
            cl2: "primary",
            cl3: "primary",
            cl4: "primary"
        }
    }

    renderHeader() {
        if (this.state.size != null) {
            return(
                <Jumbotron>
                    <div className="container">
                        <div className="row">
                            <div className="col-6">
                                <h1>Flag test</h1>
                                <h3>Check how well you know the flags!</h3>
                                <h6>Special for Savva so that he knows that 
                                I can finish things that I've started. Okay?! Enjoy!</h6>
                            </div>
                            <div className="col-6">
                            <h1>Your result is {this.state.correct} / {this.state.size}</h1>
                            </div>
                        </div>
                    </div>
                </Jumbotron>
            );
        } else {
            return(
                <Jumbotron>
                    <div className="container">
                        <div className="row">
                            <div className="col-6">
                                <h1>Flag test</h1>
                                <h3>Check how well you know the flags!</h3>
                                <h6>Special for Savva so that he knows that 
                                I can finish things that I've started. Okay?! Enjoy!</h6>
                            </div>
                        </div>
                    </div>
                </Jumbotron>
            );
        }
    }

    choseGame(size) {
        var randarr = [];
        while(randarr.length < size) {
            var r = Math.floor(Math.random() * (this.state.countries.length - 1)) + 1;
            if(randarr.indexOf(r) === -1) randarr.push(r);
        }
        this.setState({
            size: size,
            started: true,
            chosenNumbers: randarr
        });
    }

    renderContent() {
        if (!this.state.started) {
            return(
                <div className="container">
                    <div className="row">
                        <div className="col-6 offset-3">
                            <h2 className="text-center">Choose your game:</h2>
                            <div className="container mt-4">
                                <div className="row">
                                    <div className="col-6">
                                        <Button onClick={() => {
                                            {this.choseGame(5)}
                                        }} 
                                        block color="warning" size="lg">
                                            Quick</Button>
                                    </div>
                                    <div className="col-6">
                                        <Button onClick={() => {
                                            {this.choseGame(20)}
                                        }} 
                                        block color="success" size="lg">
                                            Long</Button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            );
        } else {
            if (this.state.current < this.state.size) {
                return <div>
                    {this.renderQuestion(this.state.countries[
                    this.state.chosenNumbers[
                        this.state.current]])}
                </div>
            }
            else {
                return <div>
                    <Jumbotron>
                        <h1 className="text-center">That's it!</h1>
                        <h2 className="text-center">You can leave your feedback by sending me some cute emoji in Telegram. I'll be waiting 🥰</h2>
                    </Jumbotron>
                    </div>
            }
        }
    }

    sleep(milliseconds) {
        const date = Date.now();
        let currentDate = null;
        do {
          currentDate = Date.now();
        } while (currentDate - date < milliseconds);
      }

    next() {
        this.sleep(1000);
        this.setState({
            current: this.state.current + 1,
            cl1: "primary",
            cl2: "primary",
            cl3: "primary",
            cl4: "primary"
        });

    }

    renderQuestion(country) {
        var randarr = [];
        randarr.push(country.id);
        while(randarr.length < 4) {
            var r = Math.floor(Math.random() * (this.state.countries.length - 1)) + 1;
            if(randarr.indexOf(r) === -1) randarr.push(r);
        }
        randarr.sort(() => Math.random() - 0.5);
        //alert(randarr);

        return(
            <div>
                <div className="container">
                    <div className="row">
                        <div className="col-6">
                            <img src={country.url}/>
                        </div>
                        <div className="col-6">
                            <div className="container">
                                <div className="row">
                                    <div className="col-6 col-answer">
                                        <Button onClick={() => {
                                            if (randarr[0] === country.id) {
                                                this.setState({
                                                    correct: this.state.correct + 1,
                                                    cl1: "success"
                                                });
                                            } else {
                                                this.setState({
                                                    cl1: "danger"
                                                });
                                            }
                                            this.next()
                                            }
                                        } 
                                        block size="lg" outline color={this.state.cl1}>
                                            {this.state.countries[randarr[0]].name}
                                        </Button>
                                    </div>
                                    <div className="col-6 col-answer">
                                        <Button onClick={() => {
                                            if (randarr[1] === country.id) {
                                                this.setState({
                                                    correct: this.state.correct + 1
                                                });
                                                this.setState({
                                                    cl2: "success"
                                                });
                                            } else {
                                                this.setState({
                                                    cl2: "danger"
                                                });
                                            }
                                            this.next()
                                        }}
                                        block size="lg" outline color={this.state.cl2}>
                                            {this.state.countries[randarr[1]].name}
                                        </Button>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-6 col-answer">
                                        <Button onClick={() => {
                                            if (randarr[2] === country.id) {
                                                this.setState({
                                                    correct: this.state.correct + 1
                                                });
                                                this.setState({
                                                    cl3: "success"
                                                });
                                            } else {
                                                this.setState({
                                                    cl3: "danger"
                                                });
                                            }
                                            this.next()
                                        }}
                                        block size="lg" outline color={this.state.cl3}>
                                            {this.state.countries[randarr[2]].name}
                                        </Button>
                                    </div>
                                    <div className="col-6 col-answer">
                                        <Button onClick={() => {
                                            if (randarr[3] === country.id) {
                                                this.setState({
                                                    correct: this.state.correct + 1
                                                });
                                                this.setState({
                                                    cl4: "success"
                                                });
                                            } else {
                                                this.setState({
                                                    cl4: "danger"
                                                });
                                            }
                                            this.next()
                                        }}
                                        block size="lg" outline color={this.state.cl4}>
                                            {this.state.countries[randarr[3]].name}
                                        </Button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
    
    render() {
        return(
            <div>
                {this.renderHeader()}
                {this.renderContent()}
            </div>
        );
    }
}

export default Game;