import React, { Component } from 'react';

    function Flag({ country }) {
        return(
            <div>
                <img src={country.url} alt={country.name}/>
            </div>
        );
    }

    const Flags = props => {
        const countries = props.countries.map((country) => {
            return(
                <div key={country.id}>
                    <Flag country={country}/>
                </div>
            );
        });
        return(
            <div>
                {countries}
            </div>
        );
    }

    export default Flags;